import { Model } from '../base/Model';
import { dayjs, formatNumber } from '../../utils/utils';
export class CardModel extends Model {
    constructor(data, events) {
        super(data, events);
        this.myBid = 0;
    }
    clearBid() {
        this.myBid = 0;
    }
    placeBid(value) {
        this.price = value;
        this.history = [...this.history.slice(1), value];
        this.myBid = value;
        if (value > this.minPrice * 10) {
            this.status = 'closed';
        }
        this.emitChanges('auction:changed', { id: this.id, value });
    }
    get isMyBid() {
        return this.price === this.myBid;
    }
    get isParticipate() {
        return this.myBid !== 0;
    }
    get timeStatus() {
        if (this.status === 'closed') {
            return ' ';
        }
        else {
            return dayjs.duration(dayjs(this.datetime).valueOf() - Date.now()).format('D[ ] H[ ] m[ ] s[ ]');
        }
    }
    get statusLabel() {
        const formattedDateTime = dayjs(this.datetime).format('D MMMM [] HH:mm');
        switch (this.status) {
            case 'active':
                return `  ${formattedDateTime.replace(' ', '')}`;
            case 'wait':
                return ` ${formattedDateTime}`;
            case 'closed':
                return ` ${formattedDateTime}`;
            default:
                return this.status;
        }
    }
    get auctionStatus() {
        switch (this.status) {
            case 'active':
                return `  `;
            case 'wait':
                return `  `;
            case 'closed':
                return `  ${formatNumber(this.price)} `;
            default:
                return '';
        }
    }
    get nextBid() {
        return Math.floor(this.price * 1.1);
    }
}
